import { storageService, storeService } from "fBase";
import { addDoc, collection, onSnapshot, orderBy, query } from "firebase/firestore";
import React, { useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import Nweet from "./Nweet";
import styled from "styled-components";
import { getDownloadURL, ref, uploadString } from "firebase/storage";
import { v4 as uuidv4 } from 'uuid';
import { motion } from "framer-motion";

const Wrapper = styled.div`
    width: 500px;
    height: 1000px;
    position: relative;
    top: 100px;
    margin:  0 auto;
`;
const Form = styled.form`
    display: flex;
    flex-direction: column;
`;
const Tweet = styled.input`
    height: 40px;
    border-radius: 15px;
    position: relative;
    border: 0;
    outline: 0;
    padding-left : 35px;
    background : linear-gradient(90deg,#9CC3D5,#0063B2);
`;
const Search = styled.svg`
    width: 25px;
    height: 25px;
    position: absolute;
    top: 8px;
    left: 5px;
`;
const Filechoice = styled.input`
    width: fit-content;
    padding: 15px;
    margin-top : 15px;
    border-radius: 10px;
    background: linear-gradient(90deg,#0063B2,#9CC3D5);
`;
const AlreadyImg = styled(motion.img)`
    width: 100px;
    height: 100px;
`;
const RemoveSvg = styled(motion.svg)`
    width: 40px;
    height: 40px;
    position: absolute;
    top: 65px;
    right: 20px;
    cursor: pointer;
`;
function Home({UserObj})
{
    const {register,handleSubmit,setValue} = useForm();
    const[imgUrl,SetImgUrl] = useState(null);
    const fileInput = useRef(null);
    const [ntweet,SetNtweet] = useState([]);
    const onvalid = async(data) => {
            setValue('main',"");
                if(imgUrl === null){
                    await addDoc(collection(storeService,'ntwitter'),{
                        data,
                        createdAt : Date.now(),
                        createdId : UserObj.uid,
                    });
                }else{
                    const fileRef = ref(storageService,`${UserObj.uid}/${uuidv4()}`);
                    const response = await uploadString(fileRef,imgUrl,'data_url');
                    const DownloadImg = await getDownloadURL(response.ref);
                    await addDoc(collection(storeService,'ntwitter'),{
                        data,
                        createdAt : Date.now(),
                        createdId : UserObj.uid,
                        DownloadUrl : DownloadImg,
                    });
                    fileInput.current.value=null;
                    SetImgUrl(null);
                }
                
    }
    useEffect(() => {
        const q = query(collection(storeService,'ntwitter'),orderBy('createdAt','desc'));
        onSnapshot(q, (snap) =>{
                const realtime = snap.docs.map((doc) => ({
                    id : doc.id,
                    ...doc.data(),
                }))
                SetNtweet(realtime);
        });
    },[]);
    
    const ChangeImg = async(event) => {
            const {target : {files}} = event;
            const theFiles = files[0];
            const reader = new FileReader();
            reader.onloadend = (finished) => {
                SetImgUrl(finished.currentTarget.result);
            };
            reader.readAsDataURL(theFiles);
    }
    const ClearImg = () => {
        fileInput.current.value = null;
        SetImgUrl(null);
    };
    const TrashTong = {
        start : {fill : '#9CC3D5'},
        hover : {fill : ['#9CC3D5','#0063B2','#9CC3D5'],transition : {
            duration : 2,
        }},
    }
    return (
        <Wrapper>
            <Form onSubmit={handleSubmit(onvalid)}>
                <Tweet {...register('main',{required : true})} placeholder="What's Going on~?"/>
                <Filechoice ref={fileInput} accept="images/*" type='file' onChange={ChangeImg}/>
                {imgUrl && <AlreadyImg initial={{opacity : 0}} animate={{opacity : 1,transition : {duration : 1}}} src={imgUrl}/>}
                <Search xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                    <path d="M500.3 443.7l-119.7-119.7c27.22-40.41 40.65-90.9 33.46-144.7C401.8 87.79 326.8 13.32 235.2 1.723C99.01-15.51-15.51 99.01 1.724 235.2c11.6 91.64 86.08 166.7 177.6 178.9c53.8 7.189 104.3-6.236 144.7-33.46l119.7 119.7c15.62 15.62 40.95 15.62 56.57 0C515.9 484.7 515.9 459.3 500.3 443.7zM79.1 208c0-70.58 57.42-128 128-128s128 57.42 128 128c0 70.58-57.42 128-128 128S79.1 278.6 79.1 208z"/>
                </Search>
            </Form>
                <div>
                        <RemoveSvg variants={TrashTong} initial='start' whileHover='hover' onClick={ClearImg} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
                            <motion.path d="M135.2 17.69C140.6 6.848 151.7 0 163.8 0H284.2C296.3 0 307.4 6.848 312.8 17.69L320 32H416C433.7 32 448 46.33 448 64C448 81.67 433.7 96 416 96H32C14.33 96 0 81.67 0 64C0 46.33 14.33 32 32 32H128L135.2 17.69zM31.1 128H416V448C416 483.3 387.3 512 352 512H95.1C60.65 512 31.1 483.3 31.1 448V128zM111.1 208V432C111.1 440.8 119.2 448 127.1 448C136.8 448 143.1 440.8 143.1 432V208C143.1 199.2 136.8 192 127.1 192C119.2 192 111.1 199.2 111.1 208zM207.1 208V432C207.1 440.8 215.2 448 223.1 448C232.8 448 240 440.8 240 432V208C240 199.2 232.8 192 223.1 192C215.2 192 207.1 199.2 207.1 208zM304 208V432C304 440.8 311.2 448 320 448C328.8 448 336 440.8 336 432V208C336 199.2 328.8 192 320 192C311.2 192 304 199.2 304 208z"/>
                        </RemoveSvg> 
                        {ntweet.map((twit) => <Nweet key={twit.id} isOwner = {Boolean(twit.createdId === UserObj.uid)}  ObjData ={twit}/>)}
                </div>
        </Wrapper>
    );
}
export default Home;