import { useState } from "react";
import {createUserWithEmailAndPassword,GithubAuthProvider,GoogleAuthProvider, signInWithEmailAndPassword, signInWithPopup} from "firebase/auth";
import { authService } from "fBase";
import styled from "styled-components";
import {AnimatePresence, motion} from "framer-motion";
import { useForm } from "react-hook-form";
function Auth()
{
    const Wrapper = styled.div`
        width: 80%;
        position: relative;
        top: 100px;
        margin:  0 auto;
        height: 450px;
        box-shadow: 0 20px 15px #2980b9;
    `;
    const Container = styled.div`
        width: 300px;
        margin: 15px auto;
        height: 30px;
        display: flex;
        align-items : center;
        position: relative;
        justify-content: space-between;
    `;
    const Title = styled.h1`
        text-align: center;
        font-size: 30px;
        margin-top: 20px;
    `;
    const Form = styled.form`
        width: 80%;
        margin: 0 auto;
        display: flex;
        padding-top: 30px;
        flex-direction: column;
        align-items: center;
        input{
            width: 300px;
            height: 40px;
            margin-bottom:40px;
        }
    `;
    const LoginInfo = styled.input`
        background-color: transparent;
        border: 0;
        border-bottom: 1.5px solid black;
        outline: 0;
    `;
    const ClickSpan = styled(motion.span)`
        background : linear-gradient(90deg,#0063B2,#9CC3D5);
        padding: 15px;
        border-radius: 15px;
    `;
    const Btn = styled(motion.button)`
        padding: 13px;
        background : linear-gradient(90deg,#0063B2,#9CC3D5);
        border-radius: 9px;
        border: 0;
        cursor: pointer;
    `;
    const LogInBtn = styled.input`
        background : linear-gradient(90deg,#0063B2,#9CC3D5);
        border-radius: 10px;
        &:hover{
            background : linear-gradient(90deg,#9CC3D5,#0063B2);
            color: #2ecc71;
            transition-duration: 0.6s;
        }
    `;
    const [newAccount,SetNewaacount] = useState(true);
    const onClickLogIn = async(event) => {
        const {target : {name}} = event;
            let provider;
        if(name === 'google'){
            provider = new GoogleAuthProvider();
            await signInWithPopup(authService,provider);
        }
        else if(name === 'git'){
            provider = new GithubAuthProvider();
            await signInWithPopup(authService,provider);
        }
    }
    const {register,handleSubmit} = useForm();
    const onValid = async(data) => {
        try{
                if(newAccount){
                await createUserWithEmailAndPassword(authService,data.email,data.password);
            }
            else{
                await signInWithEmailAndPassword(authService,data.email,data.password);
            }
        }catch(error){
            alert(error);
        }        
    }
    
    return (
        <Wrapper>
            <Title> Twitter</Title>
            <Form onSubmit={handleSubmit(onValid)}>
                <LoginInfo name="email" 
                placeholder="email"
                {...register('email',{required : true, minLength : 5,
                })}/>
                <LoginInfo name="password" type='password'
                {...register('password',{required : true})} placeholder="password"/>
                {}
                <LogInBtn type='submit' value={newAccount ? 'CreateAccount' : 'Log in'}/>
                <ClickSpan initial={{y : 0}} whileHover={{y : -10}} onClick={() => SetNewaacount((prev) => !prev)}>{newAccount ? '   ' : '   '}</ClickSpan>
            </Form>
            <AnimatePresence>
                <Container>
                    <Btn  onClick={onClickLogIn} name ='google'>   
                        Google
                    </Btn>   
                    <Btn onClick={onClickLogIn} name='git'>  
                        GitHub
                    </Btn> 
                </Container>
            </AnimatePresence>
        </Wrapper>
    );
}
export default Auth;
