import { storageService, storeService } from "fBase";
import { deleteDoc, doc, updateDoc } from "firebase/firestore";
import { deleteObject, ref } from "firebase/storage";
import { motion } from "framer-motion";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { useMatch, useNavigate } from "react-router-dom";
import styled from "styled-components";
import Detail from "./Detail";
const TweetList = styled(motion.div)`
    background : linear-gradient(90deg,#0063B2,#9CC3D5);
    padding: 15px;
    margin: 10px 0;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight:bold;
`;
const Flexdiv = styled.div`
    display: flex;
    justify-content: space-around;
    align-items: center;
    svg{
        cursor: pointer;
    }
`;
const Aniimg = styled(motion.img)`
    margin-left: 10px;
    width: 50px; height: 50px;
`;
const RemoveSvg = styled(motion.svg)`
    width: 40px;
    height: 40px;
    margin-right: 20px;
    color: #0063B2;
`;
const AddRemoveSvg = styled(RemoveSvg)`
    position: absolute;
    top: 6px; right: 35px;
`;
const EditSvg = styled(motion.svg)`
    width: 30px;
    height: 30px;
    color: #0063B2;
`;
const AddEditSvg = styled(EditSvg)`
    position: absolute;
    top: 10px; right: 15px;
`;
const TweetVariants = {
    start : {scale : 1,y : 0},
    hover : {scale : 1.07,y :-5},
    exit : {scale : 1}
}
const EditForm = styled(motion.form)`
    position: relative;
    width: 100%;
    height: 50px;
`;
const EditInput = styled.input`
    width: 100%;
    height: inherit;
    background-color: transparent;
    border: 0;
    border-bottom: 1.5px solid black;
    outline: 0;
`;
function Nweet({ObjData,isOwner})
{
    const [editing,SetEditing] = useState(false);
    const history = useNavigate();
    const ImgMacth = useMatch(`/Detail`);
    const RemoveClick = async() => {
        const ok = window.confirm('..  ...?');
        if(ok){
            //Delete twitter 
            const TextDleter = doc(storeService,`ntwitter`,`${ObjData.id}`)
            await deleteDoc(TextDleter);
            //    
            const ImgUrl = ref(storageService,ObjData.DownloadUrl);
            await deleteObject(ImgUrl);
        }
    }
    const ToggleEditing = () => {
        SetEditing((prev) => !prev);
    }
    const { register,handleSubmit,setValue} = useForm();
    const onValid = async(data) => {
        SetEditing(false);
        const editText = await doc(storeService, 'ntwitter', `${ObjData.id}`);
        updateDoc(editText,{
            data
        });
        setValue('main',"");
    }
    const ImgClick = () => {
                history(`/Detail`);
        }
    return (
        <>
        {editing ? 
        (<EditForm initial={{opacity : 0}} animate={{opacity : 1}} onSubmit={handleSubmit(onValid)}>
            <EditInput {...register('main',{required : true})} placeholder={`Edit ${ObjData.displayName} Tweet!`}/>
            <AddEditSvg onClick={ToggleEditing} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"> 
                <path fill="whitesmoke" d="M490.3 40.4C512.2 62.27 512.2 97.73 490.3 119.6L460.3 149.7L362.3 51.72L392.4 21.66C414.3-.2135 449.7-.2135 471.6 21.66L490.3 40.4zM172.4 241.7L339.7 74.34L437.7 172.3L270.3 339.6C264.2 345.8 256.7 350.4 248.4 353.2L159.6 382.8C150.1 385.6 141.5 383.4 135 376.1C128.6 370.5 126.4 361 129.2 352.4L158.8 263.6C161.6 255.3 166.2 247.8 172.4 241.7V241.7zM192 63.1C209.7 63.1 224 78.33 224 95.1C224 113.7 209.7 127.1 192 127.1H96C78.33 127.1 64 142.3 64 159.1V416C64 433.7 78.33 448 96 448H352C369.7 448 384 433.7 384 416V319.1C384 302.3 398.3 287.1 416 287.1C433.7 287.1 448 302.3 448 319.1V416C448 469 405 512 352 512H96C42.98 512 0 469 0 416V159.1C0 106.1 42.98 63.1 96 63.1H192z"/>
            </AddEditSvg>
            <AddRemoveSvg onClick={RemoveClick}  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
                    <path fill="whitesmoke" d="M310.6 361.4c12.5 12.5 12.5 32.75 0 45.25C304.4 412.9 296.2 416 288 416s-16.38-3.125-22.62-9.375L160 301.3L54.63 406.6C48.38 412.9 40.19 416 32 416S15.63 412.9 9.375 406.6c-12.5-12.5-12.5-32.75 0-45.25l105.4-105.4L9.375 150.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 210.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25l-105.4 105.4L310.6 361.4z"/>
                    </AddRemoveSvg>        
        </EditForm>) :  
            <TweetList initial='start' whileHover='hover' variants={TweetVariants}>
                <Flexdiv>
                    <h4>{ObjData.data.main}</h4>
                    {ObjData.DownloadUrl  ? <Aniimg key={ObjData.createdAt} layoutId={ObjData.DownloadUrl} onClick={ImgClick} src={ObjData.DownloadUrl}/> : null}
                </Flexdiv>
                {isOwner && <>
                        <Flexdiv>
                        <RemoveSvg onClick={RemoveClick}  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
                            <path fill="currentColor" d="M310.6 361.4c12.5 12.5 12.5 32.75 0 45.25C304.4 412.9 296.2 416 288 416s-16.38-3.125-22.62-9.375L160 301.3L54.63 406.6C48.38 412.9 40.19 416 32 416S15.63 412.9 9.375 406.6c-12.5-12.5-12.5-32.75 0-45.25l105.4-105.4L9.375 150.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 210.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25l-105.4 105.4L310.6 361.4z"/></RemoveSvg>                            
                            <EditSvg onClick={ToggleEditing} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"> 
                            <path fill="currentColor" d="M490.3 40.4C512.2 62.27 512.2 97.73 490.3 119.6L460.3 149.7L362.3 51.72L392.4 21.66C414.3-.2135 449.7-.2135 471.6 21.66L490.3 40.4zM172.4 241.7L339.7 74.34L437.7 172.3L270.3 339.6C264.2 345.8 256.7 350.4 248.4 353.2L159.6 382.8C150.1 385.6 141.5 383.4 135 376.1C128.6 370.5 126.4 361 129.2 352.4L158.8 263.6C161.6 255.3 166.2 247.8 172.4 241.7V241.7zM192 63.1C209.7 63.1 224 78.33 224 95.1C224 113.7 209.7 127.1 192 127.1H96C78.33 127.1 64 142.3 64 159.1V416C64 433.7 78.33 448 96 448H352C369.7 448 384 433.7 384 416V319.1C384 302.3 398.3 287.1 416 287.1C433.7 287.1 448 302.3 448 319.1V416C448 469 405 512 352 512H96C42.98 512 0 469 0 416V159.1C0 106.1 42.98 63.1 96 63.1H192z"/></EditSvg>                        
                        </Flexdiv>
                    </>
                }
            </TweetList>
            }
            {ImgMacth && ObjData.DownloadUrl && <Detail key={ObjData.createdAt} Macth={ImgMacth} ImgURl={ObjData}/>}
        </>
    );
}
export default Nweet;