import { onAuthStateChanged } from "firebase/auth";
import { useEffect, useState } from "react";
import {authService} from '../fBase' ;
import Routerr from "./Router";
function App() {
  const [init,Setinit] = useState(false);
  const [isLoading,SetLoading] = useState(authService.currentUser);
  const [UserObj,SetUserObj] = useState(null);
  useEffect(() => {
    onAuthStateChanged(authService,function(User){
      if(User){
        SetLoading(true);
        SetUserObj(User);
      }
      else{
        SetLoading(false);
      }
      Setinit(true);
    });
  },[]);
  
  return (
    <>
      {init ? <Routerr UserObj = {UserObj} Loding = {isLoading}/> : 'initialiting...'}
    </>
  );
}

export default App;
