import { Link, useMatch } from "react-router-dom";
import styled from "styled-components";
import { AnimatePresence, motion, useAnimation } from "framer-motion";
import {useViewportScroll} from "framer-motion";
const Navi = styled(motion.nav)`
    background-color: black;
    position: fixed;
    width: 100%;
    z-index: 200;
`;
const FlexUl = styled.ul`
    width: 70%;
    margin:  0 auto;
    display: flex;
    height: 50px;
    justify-content: space-around;
    align-items: center;
    position: relative;
`;
const UnderBar = styled(motion.div)`
    width: inherit;
    height: 2px;
    background-color: red;
`;
function Nav({UserObj})
{
    const {scrollY} = useViewportScroll();
    scrollY.onChange((i) => {
        if(i > 60){
            NavAnimation.start({backgroundColor : '#5CC8D7'});
        }
        else{
            NavAnimation.start({backgroundColor : 'rgba(0,0,0,0)'})
        }
    })
    const homeMacth = useMatch(`/`);
    const ProfileMacth = useMatch('/profile');
    const NavAnimation = useAnimation();
    return (
        <>
            <AnimatePresence>
                <Navi initial ={{background : 'rgba(0,0,0,0)'}} 
                    animate={NavAnimation} exit={{background : 'rgba(0,0,0,0)'}}>
                    <FlexUl>
                        <motion.li>
                            <Link to={'/'}>Home</Link>
                            { homeMacth && <UnderBar layoutId="bar"/>}
                        </motion.li> 
                        
                        <motion.li>
                            <Link to={'/profile'}>{`${UserObj.displayName || ' '} Profile`}</Link>
                            {ProfileMacth && <UnderBar layoutId="bar"/>}
                        </motion.li>
                    </FlexUl>
                </Navi>
            </AnimatePresence>
        </>
    );
}
export default Nav;