import { authService, storeService } from "fBase";
import { signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import {motion} from "framer-motion";
import styled from "styled-components";
import { collection, getDocs, orderBy, query, where } from "firebase/firestore";
import React, { useEffect, useState } from "react";
import { useCallback } from "react";

const Btn = styled(motion.button)`
    height: 70px;
    width: 140px;
    border-radius: 15px;
    background-color: transparent;
    box-shadow: 0px 10px 20px rgba(0,0,0,0.5);
    cursor: pointer;
    position: absolute;
    top: 180px;
    left: 220px;
`;
const Container = styled.div`
    background:linear-gradient(90deg,#9CC3D5,#0063B2);
    width: 100%;
    height: 150vh;
`;
const Email = styled.h2`
    height: 50px;
    font-size: 30px;
    text-align: center;
`;
const Info = styled.div`
    margin:0 auto ;
    position: relative;
    top: 100px;
    width: 600px;
    height: 600px;
    line-height: 600px;
`;
const PhotoDiv = styled.div`
    width: 500px;
    height: 500px;
    position: absolute;
    box-shadow: 0px 15px 15px #2980b9;
    left: 0; right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns:repeat(3,1fr);
    grid-template-rows: repeat(3,1fr);
    overflow: scroll;
    img{
        width: 150px;
        height: 150px;
    }
`;
const Title = styled.h3`
    padding: 20px;
    text-align: center;
    font-size: 25px;
    font-weight: bold;
`;
function Profile({UserObj})
{   
    const [ImgData,SetImgData] = useState([]);
    const history = useNavigate();
    const OnCLickLogOut = () => {
        signOut(authService);
        history('/');
    }
    const getMyTweet = useCallback(async() => {
        const q = query(collection(storeService,'ntwitter'),orderBy('createdAt','desc'),where('createdId','==',UserObj.uid));
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((snap) => {
            SetImgData((prev) => [snap.data(), ...prev]);
        })
    },[UserObj.uid]);
    useEffect(()=>{
        getMyTweet();
    },[getMyTweet]);
    return (
        <Container>
            <Info>
                <Email> your Email : {UserObj.email}</Email>
                <Btn initial={{scale : 1}} whileHover={{scale : 1.3}} onClick={OnCLickLogOut}>Log Out</Btn>
            </Info>
            <Title>{UserObj.displayName} </Title>
            <PhotoDiv>
                {ImgData.map((data,index) => 
                    data.DownloadUrl && <img alt="" key={index} src={data.DownloadUrl}/>
                )}
            </PhotoDiv>
        </Container>
    );
}
export default Profile;